import React from "react";
export const Code = ({
  hues,
  lightnesses,
  saturation,
  exportTypes,
  exportType,
  setExportType,
  className,
  hsl,
  ...props
}) => (
  <pre
    className={`bg-dark text-white p-3 rounded shadow small ${className}`}
    {...props}
  >
    <div className="btn-group" role="group" aria-label="Basic example">
      {exportTypes.map(({ name }) => (
        <button
          key={name}
          type="button"
          onClick={() => setExportType(name)}
          className={`btn btn-sm btn-outline-light ${
            exportType === name ? "bg-light text-dark" : ""
          }`}
        >
          {name.toUpperCase()}
        </button>
      ))}
    </div>
    {"\n\n"}
    <code>
      {exportTypes.find(_ => _.name === exportType).toString({
        hues,
        lightnesses,
        saturation,
        hsl
      })}
    </code>
  </pre>
);
